.ipynb-renderer-root div.input_area {
  padding-right: 0 !important;
}
.ipynb-renderer-root div.output_subarea {
  padding: 0 !important;
  margin-top: 1.5px;
}
.ipynb-renderer-root div.highlight {
  background-color: #fbfbfb;
  border: solid 1px #f0f0f0;
}
.ipynb-renderer-root div.highlight pre {
  margin: 10px 0 !important;
}
.ipynb-renderer-root .output_prompt {
  border-right: none !important;
}
.ipynb-renderer-root .input_prompt {
  padding-right: 0 !important;
}
.ipynb-renderer-root div.prompt.input_prompt,
.ipynb-renderer-root div.prompt.output_prompt {
  width: 80px !important;
  min-width: 80px !important;
}

/* CUSTOM CHANGES */
:root {
  --header-color: #000;
}

span {
  color: #353535; /* Same color as <p> */
}

/* Change light purple tokens to darker purple */
/* e.g. numbers and '=' */
span.token[style="color: rgb(167, 122, 254);"] {
  color: #aa22ff !important;
}

/* Change light gray tokens to navy */
/* e.g. '.', ':', '()' */
span.token[style="color: rgb(190, 190, 197);"] {
  color: #0055aa !important;
}

/* Change light yellow tokens to dark red */
/* e.g. strings and block comments */
span.token[style="color: rgb(230, 208, 108);"] {
  color: #ba2121 !important;
}

/* Change comment color to classic green */
span.token[style="color: rgb(111, 112, 94);"] {
  color: #008001 !important;
}

/* Change medium pink tokens to classic green */
/* e.g. from, import, def, return */
span.token[style="color: rgb(239, 59, 125);"] {
  color: #b82f64 !important;
  font-weight: bold;
}

.no-input-number .ipynb-renderer-root div.prompt {
  display: none;
}
